.carousel-indicators{
  position:absolute;
  top:0;
  bottom:0;
  margin:auto;
  left:auto;
  width:auto;
}
.carousel-indicators li{width: 20px;
  position:relative;
  border:3px solid #470804;
  height: 19px;
  margin-bottom:11px;
  border-radius: 100%;
  background: gray;
  transition: background 0.25ms ease;
  animation: pulse 2s infinite;
}
.carousel-indicators li:hover {
  animation: none;
}
.carousel-indicators li.active{background:white;
}
.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 700ms ease-in;
}
.item-appear {
  opacity: 0;
}
.item-appear-active {
  opacity: 1;
  transition: opacity 700ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}

.slide-enter {
  opacity: 0;
}
.slide-enter-active {
  opacity: 1;
  transition: opacity 900ms ease-in;
}
.slide-appear {
  opacity: 0;
}
.slide-appear-active {
  opacity: 1;
  transition: opacity 1000ms ease-in;
}
.slide-exit {
  opacity: 1;
}
.slide-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}


@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 #470804;
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(204,169,44, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(204,169,44, 0);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 #470804;
    box-shadow: 0 0 0 0 #470804;
  }
  70% {
    -moz-box-shadow: 0 0 0 10px rgba(204,169,44, 0);
    box-shadow: 0 0 0 10px rgba(204,169,44, 0);
  }
  100% {
    -moz-box-shadow: 0 0 0 0 rgba(204,169,44, 0);
    box-shadow: 0 0 0 0 rgba(204,169,44, 0);
  }
}
